.app-wrapper {
  display: grid;

  width: 1200px;
  margin: auto;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}
.app-wrapper-content {
  grid-area: c;
  background-color: rgb(120, 120, 172);
}
.demo-logo img {
  max-width: 50px;
  padding-top: 8px;
}